body {
    padding: 0;
    margin: 0;
}


.foresome_base {
    width: 100%;
    height: max-content;


    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

}



.foresome_home_navbar {
    height: 10vh;
    border-bottom: 1px solid rgba(212, 224, 217, 1);

    width: 100%;


    display: flex;
    align-items: center;
    justify-content: space-around;

    margin-bottom: 5vh;

}



.logo {
    width: 8rem;
    height: 80%;
}





.download_img_div {
    width: 25rem;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;

}



.download_img {
    width: 35%;
    height: 60%;
}



















/* PAGE */






.foresome_screen1 {
    height: 85vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;




}



.screen1_left {
    width: 35%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}



.screen1_right {
    width: 25%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
}




.mobile1 {
    width: 100%;
    height: 100%;
}


.screen1_heading {
    color: rgba(23, 107, 58, 1);
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    font-size: 2.5rem;
    letter-spacing: 1px;


}






.screen1_text {
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    color: #031F0F;
    margin: 0;
}






.hockey_img {

    position: absolute;
    width: 50%;

    left: 0rem;
    top: 10vh;



}